import utils, { XHRResponse } from '../utils/utils';
import { blockTransfers } from './blockTransfers';
import HttpClient from '../utils/http/HttpClient';
import { Logger, LogLevel } from '../utils/Logger';

/**
 * Private Actions
 * DO NOT Export outside of api.
 * @private
 */
const actions = {
  /**
   * Sends a request using an HTTP client and the given parameters to build it.
   * @param {String} method - the request method (GET, POST etc)
   * @param {String} url - the URL where the request will be sent to
   * @param {Object} params - parameters for template
   * @param {Object} customHeaders - allow custom header for caller to be passed - i.e. content-type for different type of POST
   * @param {Object} body - the body of the request
   * @param {Number} retryCount - the number of times the request should be retried before giving up. Minimum value is 1, and indicates
   *  that requests should not be retried. Default: 1.
   * @param {Function} retryFunction - if provided, will be called with a single status code argument whenever the request fails. If
   *  the function returns true then the process will continue retrying the request; otherwise it will stop retrying and throw
   *  the error. Default: not provided.
   * @param {Object} customOptions - if provided, additional raw HTTP options to pass to the http client.
   * @returns {Promise<*>}
   */
  doFetchWithOptions: async (
    method = 'GET',
    url: string,
    params = {},
    customHeaders = {},
    body?: any,
    retryCount = 1,
    retryFunction: Function = () => {},
    customOptions = {},
  ) => {
    const options = {
      method: method,
      headers: customHeaders,
      ...customOptions,
      body,
      url: utils.fillTemplate(url, params),
    };
    if (body) {
      options.body = JSON.stringify(body);
    }
    Logger.log(LogLevel.INFO, {
      message: ` > pc ${method} ${options.url}`,
      requestUrl: options.url,
      requestMethod: method,
    });
    const httpClient = new HttpClient();
    let response;
    try {
      response = await httpClient.submitRequest(options, {
        retryCount,
        retryFunction,
      });
    } catch (e) {
      Logger.log(LogLevel.SEVERE, {
        message: ` < pc failed to send ${options.url}`,
        requestUrl: options.url,
        requestMethod: method,
      });
      throw e;
    }

    const { status, headers: respHeaders } = response;
    const respRequestId = respHeaders.has('x-request-id')
      ? respHeaders.get('x-request-id')
      : 'NO ID';
    const logLevel = status < 400 ? LogLevel.INFO : LogLevel.SEVERE;
    Logger.log(logLevel, {
      message: ` < pc ${method} ${status} [${respRequestId}] ${options.url}`,
      requestUrl: options.url,
      requestMethod: method,
      responseStatus: status,
      xRequestId: respRequestId,
    });

    return response;
  },
  /**
   * Sends a request using an HTTP client and the given parameters to build it.
   * @param {String} method - the request method (GET, POST etc)
   * @param {String} url - the URL where the request will be sent to
   * @param {Object} ims object with token and apiKey
   * @param {Object} params - parameters for template
   * @param {Object} customHeaders - allow custom header for caller to be passed - i.e. content-type for different type of POST
   * @param {String}cacheMode - the cache mode to be used (default, no-cache, reload, force-cache, only-if-cached)
   * @param {Object} body - the body of the request
   * @param {Number} retryCount - the number of times the request should be retried before giving up. Minimum value is 1, and indicates
   *  that requests should not be retried. Default: 1.
   * @param {Function} retryFunction - if provided, will be called with a single status code argument whenever the request fails. If
   *  the function returns true then the process will continue retrying the request; otherwise it will stop retrying and throw
   *  the error. Default: not provided.
   * @returns {Promise<*>}
   */
  doFetch: async (
    method = 'GET',
    url,
    ims,
    params = {},
    customHeaders = {},
    body?: any,
    cacheMode = 'default',
    retryCount = 1,
    retryFunction: Function = () => {},
  ) => {
    const requestHeaders = utils.getHeader(customHeaders, ims);
    return actions.doFetchWithOptions(
      method,
      url,
      params,
      requestHeaders,
      body,
      retryCount,
      retryFunction,
      {
        cache: cacheMode,
      },
    );
  },

  doGet: async (
    url,
    ims,
    params = {},
    customHeaders = {},
    cacheMode = 'default',
    retryCount = 1,
    retryFunction = () => {},
  ) => {
    const response = await actions.doFetch(
      'GET',
      url,
      ims,
      params,
      customHeaders,
      undefined,
      cacheMode,
      retryCount,
      retryFunction,
    );
    return response;
  },
  doHead: async (url, ims, params = {}, customHeaders = {}) => {
    const response = await actions.doFetch('HEAD', url, ims, params, customHeaders);
    return response;
  },
  doPost: async (
    url,
    ims,
    params = {},
    customHeaders = {},
    payload?: any,
    cacheMode = 'default',
    retryCount = 0,
    retryFunction: Function | undefined = undefined,
  ) => {
    const response = await actions.doFetch(
      'POST',
      url,
      ims,
      params,
      customHeaders,
      payload,
      cacheMode,
      retryCount,
      retryFunction,
    );
    return response;
  },
  /**
   * handles usecase https://git.corp.adobe.com/pages/caf/api-spec/chapters/advanced/asynchronous_invocation.html
   */
  /**
   * @description: Do poll
   * @param {String} url - the URL where the request will be sent to
   * @param {Object} ims object with token and apiKey
   * @param {Object} customHeaders - allow custom header for caller to be passed - i.e. content-type for different type of POST
   */
  doPoll: async (url, ims, customHeaders = {}) => {
    const maxRetryInterval = 3000;
    let interval = maxRetryInterval;
    let getResponse;

    do {
      getResponse = await actions.doGet(url, ims, {}, customHeaders, 'default', 0);
      if (getResponse.headers.has('retry-after')) {
        // if the response header include `retry-after`, then use that as delay interval
        interval = getResponse.headers.get('retry-after');
        // aem backend returns interval in seconds instead of milliseconds. R-API spec doesn't specify
        // whether to use seconds or milliseconds, so a sanity check is needed here
        interval = interval * 1000 > maxRetryInterval ? maxRetryInterval : interval * 1000;
      }

      const promiseTimeout = (delay) =>
        new Promise((resolve) => {
          setTimeout(resolve, delay);
        });
      await promiseTimeout(interval);
    } while (getResponse && getResponse.status === 202);

    return getResponse;
  },
  /**
   * handles usecase https://git.corp.adobe.com/pages/caf/api-spec/chapters/advanced/asynchronous_invocation.html
   */
  doPostAsync: async (url, ims, params = {}, customHeaders = {}, payload?: any) => {
    const response = await actions.doPost(url, ims, params, customHeaders, payload);

    /*
     * handles usecase https://git.corp.adobe.com/pages/caf/api-spec/chapters/advanced/asynchronous_invocation.html
     */
    if (
      response &&
      response.status === 202 &&
      response.headers &&
      response.headers.has('location')
    ) {
      const location = response.headers.get('location');
      return actions.doPoll(location, ims, customHeaders);
    }

    return response;
  },
  doDelete: async (url, ims, params = {}, customHeaders = {}, payload?: any) => {
    const response = await actions.doFetch('DELETE', url, ims, params, customHeaders, payload);
    return response;
  },
  doPut: async (url, ims, params = {}, customHeaders = {}, payload?: any) => {
    const response = await actions.doFetch('PUT', url, ims, params, customHeaders, payload);
    return response;
  },
  doPatch: async (url, ims, params = {}, customHeaders = {}, payload?: any) => {
    const response = await actions.doFetch('PATCH', url, ims, params, customHeaders, payload);
    return response;
  },
  /**
   * XHR POST call
   * @param {string} url
   * @param {Object} ims object with token and apiKey
   * @param {Object} params parameters for template
   * @param {Object} customHeaders - allow custom header for caller to be passed - i.e. content-type for different type of POST
   * @param {ArrayBuffer} payload
   * @param {UploadOptions} options and callbacks for upload
   * @returns {Promise<*>}
   */
  doXhrPost: async (url, ims, params = {}, customHeaders = {}, payload = null, options) => {
    const headers = utils.getHeader(customHeaders, ims);

    const filledTemplate = utils.fillTemplate(url, params);

    const postResponse = await new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest();
      xhr.open('POST', filledTemplate, true);
      for (const name in headers) {
        // eslint-disable-next-line no-prototype-builtins
        if (headers.hasOwnProperty(name) && name.toLowerCase() !== 'content-length') {
          xhr.setRequestHeader(name, headers[name]);
        }
      }

      // eslint-disable-next-line no-undef
      if (!global?.isNodeEnv) {
        xhr.upload.addEventListener('progress', (e) => options.onProgress && options.onProgress(e));
      }

      xhr.onerror = (e) => {
        reject(e);
      };

      xhr.onreadystatechange = () =>
        utils.returnXHRResponse(xhr, 'POST', resolve, reject, filledTemplate);

      Logger.log(LogLevel.INFO, {
        message: ` > pc POST ${url}`,
        requestUrl: url,
        requestMethod: 'POST',
        filledTemplate: filledTemplate,
      });

      xhr.send(payload || null);
    });

    return postResponse;
  },
  /**
   * XHR POST call
   * @param {string} url
   * @param {Object} ims object with token and apiKey
   * @param {Object} params parameters for template
   * @param {Object} customHeaders - allow custom header for caller to be passed - i.e. content-type for different type of POST
   * @param {ArrayBuffer} payload
   * @param {Object} options and callbacks for upload
   * @returns {Promise<XHRResponse>}
   */
  doXhrPut: async (url, ims, params = {}, customHeaders = {}, payload = null, options) => {
    let headers = {};

    if (options.update) {
      headers = utils.getHeader(customHeaders, ims);
    }

    const filledTemplate = utils.fillTemplate(url, params);

    const putResponse: XHRResponse = await new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest();
      for (const name in headers) {
        // eslint-disable-next-line no-prototype-builtins
        if (headers.hasOwnProperty(name) && name.toLowerCase() !== 'content-length') {
          xhr.setRequestHeader(name, headers[name]);
        }
      }

      // eslint-disable-next-line no-undef
      if (!global?.isNodeEnv) {
        xhr.upload.addEventListener('progress', (e) => options.onProgress && options.onProgress(e));
      }
      xhr.onerror = (e) => {
        reject(e);
      };

      xhr.onreadystatechange = () =>
        utils.returnXHRResponse(xhr, 'PUT', resolve, reject, filledTemplate);

      Logger.log(LogLevel.INFO, {
        message: ` > pc PUT ${url}`,
        requestUrl: url,
        requestMethod: 'PUT',
        filledTemplate: filledTemplate,
      });

      xhr.open('PUT', filledTemplate, true);
      xhr.send(payload || null);
      blockTransfers.addXhrPut({ path: filledTemplate, xhr });
    });

    return putResponse;
  },
};

export default actions;
