/* eslint-disable complexity */

/**
 * @module platform-connector
 */

/**
 * Utility function to get a mimetype based on the file extension
 * <p>For example.</p>
 * <ul>
 *     <li>jpg ->  image/jpeg</li>
 *     <li>png -> image/png</li>
 *     <li>mp4 -> video/mp4</li>
 * </ul>
 * @param {string} ext
 * @return {string}
 */
export function getMimeTypeFromExtension(ext) {
  ext = ext.toLowerCase();
  let ret = '';

  switch (ext) {
    case 'jpeg':
      ret = 'image/jpeg';
      break;
    case 'jpg':
      ret = 'image/jpg';
      break;
    case 'png':
      ret = 'image/png';
      break;
    case 'tiff':
      ret = 'image/tiff';
      break;
    case 'bmp':
      ret = 'image/bmp';
      break;
    case 'psd':
      ret = 'image/psd';
      break;
    case 'gif':
      ret = 'image/gif';
      break;
    case 'pdf':
      ret = 'application/pdf';
      break;
    case 'eps':
      ret = 'image/x-eps';
      break;
    case 'ai':
      ret = 'application/illustrator';
      break;
    case 'txt':
      ret = 'text/plain';
      break;
    case 'rtf':
      ret = 'text/rtf';
      break;
    case 'ppt':
    case 'pptx':
      ret = 'application/mspowerpoint';
      break;
    case 'doc':
    case 'docx':
      ret = 'application/msword';
      break;
    case 'xls':
    case 'xlsx':
      ret = 'application/msexcel';
      break;
    case 'indd':
      ret = 'application/x-indesign';
      break;
    case 'raw':
      ret = 'image/raw';
      break;
    case 'avi':
      ret = 'video/msvideo';
      break;
    case 'flv':
      ret = 'video/x-flv';
      break;
    case 'mp4':
      ret = 'video/mp4';
      break;
    case 'mpeg':
      ret = 'video/mpeg';
      break;
    case 'wmv':
      ret = 'video/x-ms-wmv';
      break;
    case 'psb':
      ret = 'application/x-photoshop';
      break;
    case 'f4v':
      ret = 'video/x-f4v';
      break;
    case 'asf':
      ret = 'application/vnd.ms-asf';
      break;
    case '3gp':
      ret = 'video/3gpp';
      break;
    case 'swf':
      ret = 'application/x-shockwave-flash';
      break;
    default:
      ret = 'application/octet-stream';
  }

  return ret;
}
